/**
 * @description gen uploader
 * @author wangfupeng
 */

import Uppy from '@uppy/core'
import XHRUpload from '@uppy/xhr-upload'

import { addQueryToUrl } from '../utils/util'
import { IUploadConfig } from './interface'

function createUploader(config: IUploadConfig): Uppy {
  // 
  const {
    server = '',
    fieldName = '',
    maxFileSize = 10 * 1024 * 1024, // 10M
    maxNumberOfFiles = 100, // 
    meta = {},
    metaWithUrl = false,
    headers = {},
    withCredentials = false,
    timeout = 10 * 1000, // 10s
    onBeforeUpload = files => files,
    onSuccess = (_file, _res) => {
      /* on success */
    },
    onError = (file, err, res?) => {
      console.error(`${file.name} upload error`, err, res)
    },
    onProgress = _progress => {
      /* on progress */
    },
  } = config

  // 
  if (!server) {
    throw new Error('Cannot get upload server address\n')
  }
  if (!fieldName) {
    throw new Error('Cannot get fieldName\n fieldName')
  }

  //  url 
  let url = server

  if (metaWithUrl) {
    url = addQueryToUrl(url, meta)
  }

  //  uppy  https://uppy.io/docs/uppy/
  const uppy = new Uppy({
    onBeforeUpload,
    restrictions: {
      maxFileSize,
      maxNumberOfFiles,
    },
    meta, //  formData 
    ...config.uppyConfig, //  Uppy 
  }).use(XHRUpload, {
    endpoint: url,
    headers,
    formData: true,
    fieldName,
    bundle: true,
    withCredentials,
    timeout,
    ...config.xhrConfig, //  XHRUpload 
  })

  //  callback
  // onSuccess  file  complete  files 
  uppy.on('complete', result => {
    const file = result.successful[0]
    const { response } = file
    const { body = {} } = response ?? {}

    try {
      onSuccess(file, body)
    } catch (err) {
      console.error('wangEditor upload file - onSuccess error', err)
    }
  })

  uppy.on('progress', progress => {
    // progress  0 - 100
    if (progress < 1) { return }
    onProgress(progress)
  })

  // uppy.on('error', error => {
  //   console.error('wangEditor file upload error', error.stack)
  // })

  uppy.on('upload-error', (file, error, response) => {
    try {
      //  try catch 
      onError(file, error, response)
    } catch (err) {
      console.error('wangEditor upload file - onError error', err)
    }
  })

  uppy.on('restriction-failed', (file, error) => {
    try {
      //  try catch 
      // @ts-ignore
      onError(file, error)
    } catch (err) {
      console.error('wangEditor upload file - onError error', err)
    }
  })

  // 
  return uppy
}

export default createUploader
