import { nanoid } from 'nanoid'

/**
 * 
 * @param prefix 
 * @returns 
 */
export function genRandomStr(prefix = 'r'): string {
  return `${prefix}-${nanoid()}`
}

export function replaceSymbols(str: string) {
  return str.replace(/</g, '&lt;').replace(/>/g, '&gt;')
}
