import { IUploadImageConfig } from '@wangeditor-next/core'

// 
export function genUploadImageConfig(): IUploadImageConfig {
  return {
    server: '', // server API 

    fieldName: 'wangeditor-uploaded-image', // formData  key
    maxFileSize: 2 * 1024 * 1024, // 2M
    maxNumberOfFiles: 100, //  xx 
    allowedFileTypes: ['image/*'],
    meta: {
      //  token  formData 
      // token: 'xxxxx', x: 100
    },
    metaWithUrl: false,
    // headers: {
    //   //  http headers
    //   // Accept: 'text/x-json', a: 100,
    // },
    withCredentials: false,
    timeout: 10 * 1000, // 10s

    onBeforeUpload: (files: any) => files, //  false 
    onProgress: (_progress: number) => {
      /* on progress */
    },
    onSuccess: (_file: any, _res: any) => {
      /* on success */
    },
    onFailed: (file: any, res: any) => {
      console.error(`'${file.name}' upload failed`, res)
    },
    onError: (file: any, err: any, res: any) => {
      /* on error */
      /* on timeout */
      console.error(`'${file.name}' upload error`, res)
    },

    // 
    // customInsert: (res, insertFn) => {},

    // 
    // customUpload: (file, insertFn) => {},

    //  xxx  base64
    base64LimitSize: 0,

    //  
    // customBrowseAndUpload: insertFn => {},
  }
}
