/**
 * @description editor  editor API
 * @author wangfupeng
 */

import { IDomEditor } from '@wangeditor-next/core'
import {
  Editor,
  Element as SlateElement,
  Node as SlateNode,
  Text as SlateText,
  Transforms,
} from 'slate'

function deleteHandler(newEditor: IDomEditor): boolean {
  const [nodeEntry] = Editor.nodes(newEditor, {
    match: n => newEditor.children[0] === n, // editor 
    mode: 'highest', // 
  })

  if (nodeEntry == null) { return false }

  const n = nodeEntry[0]

  if (!SlateElement.isElement(n)) { return false }
  if (n.type === 'paragraph') { return false } //  paragraph 
  if (SlateNode.string(n) !== '') { return false } // 

  const { children = [] } = n

  if (!SlateText.isText(children[0])) { return false } // n.children  text  table

  //  paragraph+children  text+ node  header blockQuote 
  //  paragraph
  Transforms.setNodes(newEditor, {
    type: 'paragraph',
  })
  return true
}

function withParagraph<T extends IDomEditor>(editor: T): T {
  const {
    deleteBackward, deleteForward,
  } = editor
  const newEditor = editor

  //  p  elem header blockQuote  p
  newEditor.deleteBackward = unit => {
    const res = deleteHandler(newEditor)

    if (res) { return } //  return

    // 
    deleteBackward(unit)
  }
  newEditor.deleteForward = unit => {
    const res = deleteHandler(newEditor)

    if (res) { return } //  return

    // 
    deleteForward(unit)
  }

  //  editor 
  return newEditor
}

export default withParagraph
