/**
 * @description editor  editor API
 * @author wangfupeng
 */

import { DomEditor, IDomEditor } from '@wangeditor-next/core'
import isUrl from 'is-url'
import { Editor, Node, Transforms } from 'slate'

import { insertLink, isMenuDisabled } from './helper'

function withLink<T extends IDomEditor>(editor: T): T {
  const {
    isInline, insertData, normalizeNode,
  } = editor
  const newEditor = editor

  //  isInline
  newEditor.isInline = elem => {
    const { type } = elem

    if (type === 'link') {
      return true
    }

    return isInline(elem)
  }

  //  insertData 
  newEditor.insertData = (data: DataTransfer) => {
    const text = data.getData('text/plain')

    if (!isUrl(text)) {
      // 
      insertData(data)
      return
    }
    // 
    if (/<img[^>]+>/.test(data.getData('text/html'))) {
      insertData(data)
      return
    }

    // 
    if (isMenuDisabled(newEditor)) { return } // disabled
    const { selection } = newEditor

    if (selection == null) { return }
    const selectedText = Editor.string(newEditor, selection) // 

    insertLink(newEditor, selectedText, text)
  }

  newEditor.normalizeNode = ([node, path]) => {
    const type = DomEditor.getNodeType(node)

    if (type !== 'link') {
      //  link  normalizeNode
      return normalizeNode([node, path])
    }

    // 
    const str = Node.string(node)

    if (str === '') {
      return Transforms.removeNodes(newEditor, { at: path })
    }

    return normalizeNode([node, path])
  }

  //  editor 
  return newEditor
}

export default withLink
