/**
 * @description base button class
 * @author wangfupeng
 */

import { IButtonMenu, IDropPanelMenu, IModalMenu } from '../interface'
import $, { Dom7Array } from '../../utils/dom'
import { IBarItem, getEditorInstance } from './index'
import { clearSvgStyle } from '../helpers/helpers'
import { promiseResolveThen } from '../../utils/util'
import { addTooltip } from './tooltip'

abstract class BaseButton implements IBarItem {
  readonly $elem: Dom7Array = $(`<div class="w-e-bar-item"></div>`)
  protected readonly $button: Dom7Array = $(`<button type="button"></button>`)
  menu: IButtonMenu | IDropPanelMenu | IModalMenu
  private disabled = false

  constructor(key: string, menu: IButtonMenu | IDropPanelMenu | IModalMenu, inGroup = false) {
    this.menu = menu

    //  tag
    const { tag, width } = menu
    if (tag !== 'button') throw new Error(`Invalid tag '${tag}', expected 'button'`)

    // -----------------  dom -----------------
    const { title, hotkey = '', iconSvg = '' } = menu
    const { $button } = this
    if (iconSvg) {
      const $svg = $(iconSvg)
      clearSvgStyle($svg) //  svg svg 
      $button.append($svg)
    } else {
      //  icon  title
      $button.text(title)
    }
    addTooltip($button, iconSvg, title, hotkey, inGroup) //  tooltip
    if (inGroup && iconSvg) {
      // in groupButton icon menu title
      //  icon  title 
      $button.append($(`<span class="title">${title}</span>`))
    }
    if (width) {
      $button.css('width', `${width}px`)
    }
    $button.attr('data-menu-key', key) // menu key
    this.$elem.append($button)

    // -----------------  -----------------
    promiseResolveThen(() => this.init())
  }

  private init() {
    //  button 
    this.setActive()
    this.setDisabled()

    // button click
    this.$button.on('click', e => {
      e.preventDefault()
      const editor = getEditorInstance(this)

      editor.hidePanelOrModal() //  panel

      if (this.disabled) return

      this.exec() //  menu.exec
      this.onButtonClick() // 
    })
  }

  /**
   *  menu.exec
   */
  private exec() {
    const editor = getEditorInstance(this)
    const menu = this.menu
    const value = menu.getValue(editor)
    this.setIcon()
    this.setTooltip()
    menu.exec(editor, value)
  }

  // 
  abstract onButtonClick(): void

  private setActive() {
    const editor = getEditorInstance(this)
    const { $button } = this
    const active = this.menu.isActive(editor)

    const className = 'active'
    if (active) {
      //  active
      $button.addClass(className)
    } else {
      //  active
      $button.removeClass(className)
    }
  }

  private setDisabled() {
    const editor = getEditorInstance(this)
    const { $button } = this
    let disabled = this.menu.isDisabled(editor)

    if (editor.selection == null || editor.isDisabled()) {
      //  readOnly  disabled
      disabled = true
    }

    //  enable
    if (this.menu.alwaysEnable) disabled = false

    const className = 'disabled'
    if (disabled) {
      //  disabled
      $button.addClass(className)
    } else {
      //  disabled
      $button.removeClass(className)
    }

    this.disabled = disabled // 
  }

  private setIcon() {
    const editor = getEditorInstance(this)
    const { $button } = this
    if (!this.menu.getIcon) return
    const iconSvg = this.menu.getIcon(editor)

    if (iconSvg) {
      $button.find('svg').remove()
      const $svg = $(iconSvg)
      clearSvgStyle($svg)
      $button.append($svg)
    }
  }

  private setTooltip() {
    const editor = getEditorInstance(this)
    const { $button } = this
    if (!this.menu.getTitle) return
    const title = this.menu.getTitle(editor)
    const iconSvg = this.menu.iconSvg
    if (title && iconSvg) {
      addTooltip($button, iconSvg, title)
    }
  }

  changeMenuState() {
    this.setActive()
    this.setDisabled()
  }
}

export default BaseButton
