/**
 * @description editor  editor API
 * @author wangfupeng
 */

import { DomEditor, IDomEditor } from '@wangeditor-next/core'
import { Editor, Transforms } from 'slate'

function withHeader<T extends IDomEditor>(editor: T): T {
  const { insertBreak } = editor
  const newEditor = editor

  //  insertBreak - header  paragraph
  newEditor.insertBreak = () => {
    const [match] = Editor.nodes(newEditor, {
      match: n => {
        const type = DomEditor.getNodeType(n)

        return type.startsWith('header') //  node.type  header  node
      },
      universal: true,
    })

    if (!match) {
      // 
      insertBreak()
      return
    }

    const isAtLineEnd = DomEditor.isSelectionAtLineEnd(editor, match[1])

    //  p
    if (isAtLineEnd) {
      const p = { type: 'paragraph', children: [{ text: '' }] }

      Transforms.insertNodes(newEditor, p, { mode: 'highest' })
    } else {
      insertBreak()
    }
  }

  //  editor 
  return newEditor
}

export default withHeader
