/**
 * @description tooltip 
 * @author wangfupeng
 */

import { Dom7Array } from '../../utils/dom'
import { IS_APPLE } from '../../utils/ua'

export function addTooltip(
  $button: Dom7Array,
  iconSvg: string,
  title: string,
  hotkey: string = '',
  inGroup = false
) {
  if (!iconSvg) {
    //  icon  title  tooltip
    return
  }

  if (hotkey) {
    const fnKey = IS_APPLE ? 'cmd' : 'ctrl' // mac OS  cmd windows  ctrl
    hotkey = hotkey.replace('mod', fnKey)
  }

  if (inGroup) {
    // in groupButton tooltip  
    if (hotkey) {
      $button.attr('data-tooltip', hotkey)
      $button.addClass('w-e-menu-tooltip-v5')
      $button.addClass('tooltip-right') // tooltip 
    }
  } else {
    //  in groupButton  tooltip
    const tooltip = hotkey ? `${title}\n${hotkey}` : title
    $button.attr('data-tooltip', tooltip)
    $button.addClass('w-e-menu-tooltip-v5')
  }
}
