/**
 * @description image render elem
 * @author wangfupeng
 */

import { DomEditor, IDomEditor } from '@wangeditor-next/core'
import throttle from 'lodash.throttle'
import { Element as SlateElement, Transforms } from 'slate'
// eslint-disable-next-line @typescript-eslint/no-unused-vars
import { jsx, VNode } from 'snabbdom'

import $, { Dom7Array } from '../../utils/dom'
import { ImageElement } from './custom-types'

interface IImageSize {
  width?: string
  height?: string
}

function genContainerId(editor: IDomEditor, elemNode: SlateElement) {
  const { id } = DomEditor.findKey(editor, elemNode) // node  id

  return `w-e-image-container-${id}`
}

/**
 *  image container
 */
function renderContainer(
  editor: IDomEditor,
  elemNode: SlateElement,
  imageVnode: VNode,
  imageInfo: IImageSize,
): VNode {
  const { width, height } = imageInfo

  const style: any = {}

  if (width) { style.width = width }
  /**  */
  if (height) { style.height = height }

  const containerId = genContainerId(editor, elemNode)

  return (
    <div id={containerId} style={style} className="w-e-image-container">
      {imageVnode}
    </div>
  )
}

/**
 *  image container
 */
function renderResizeContainer(
  editor: IDomEditor,
  elemNode: SlateElement,
  imageVnode: VNode,
  imageInfo: IImageSize,
) {
  const $body = $('body')
  const containerId = genContainerId(editor, elemNode)
  const { width, height } = imageInfo

  let originalX = 0
  let originalWith = 0
  let originalHeight = 0
  let maxWidth = 0 // 
  let revers = false //  right-top  left-top 
  let $container: Dom7Array | null = null

  function getContainerElem(): Dom7Array {
    const $containerFromDom = $(`#${containerId}`)

    if ($containerFromDom.length === 0) {
      throw new Error('Cannot find image container elem')
    }
    return $containerFromDom
  }

  // mouseover callback 
  const onMousemove = throttle((e: Event) => {
    e.preventDefault()

    const { clientX } = e as MouseEvent
    const gap = revers ? originalX - clientX : clientX - originalX // 
    const newWidth = originalWith + gap
    const newHeight = originalHeight * (newWidth / originalWith) //  width  height

    /**
     * 3px margin
     */
    if (newWidth > maxWidth - 6) { return } // 

    //  img  - DOM mouseup  node
    if ($container == null) { return }
    if (newWidth <= 15 || newHeight <= 15) { return } //  15px

    $container.css('width', `${newWidth}px`)
    $container.css('height', `${newHeight}px`)
  }, 100)

  function onMouseup(_e: Event) {
    //  mousemove
    $body.off('mousemove', onMousemove)

    if ($container == null) { return }
    const newWidth = $container.width().toFixed(2)
    const newHeight = $container.height().toFixed(2)

    //  node
    const props: Partial<ImageElement> = {
      style: {
        ...(elemNode as ImageElement).style,
        width: `${newWidth}px`,
        height: `${newHeight}px`,
      },
    }

    Transforms.setNodes(editor, props, { at: DomEditor.findPath(editor, elemNode) })

    //  mouseup
    $body.off('mouseup', onMouseup)
  }

  /**
   * 
   */
  function init(clientX: number, parentNodeWidth: number) {
    $container = getContainerElem()

    //  x 
    originalX = clientX
    maxWidth = parentNodeWidth

    //  img 
    const $img = $container.find('img')

    if ($img.length === 0) { throw new Error('Cannot find image elem') }
    originalWith = $img.width()
    originalHeight = $img.height()

    //  mousemove
    $body.on('mousemove', onMousemove)

    //  mouseup
    $body.on('mouseup', onMouseup)

    //  hoverbar
    const hoverbar = DomEditor.getHoverbar(editor)

    if (hoverbar) { hoverbar.hideAndClean() }
  }

  const divStyle: any = {}

  if (width) { divStyle.width = width }
  if (height) { divStyle.height = height }
  divStyle.boxShadow = '0 0 0 1px #B4D5FF' //  selected 

  return (
    <div
      id={containerId}
      style={divStyle}
      className="w-e-image-container w-e-selected-image-container"
      on={{
        //  mousedown 
        mousedown: (e: MouseEvent) => {
          const $target = $(e.target as Element)

          if (!$target.hasClass('w-e-image-dragger')) {
            // target  .w-e-image-dragger 
            return
          }
          e.preventDefault()

          if ($target.hasClass('left-top') || $target.hasClass('left-bottom')) {
            revers = true // 
          }

          //  image 
          const parentNode = DomEditor.getParentNode(editor, elemNode)

          if (parentNode == null) { return }
          const parentNodeDom = DomEditor.toDOMNode(editor, parentNode)
          const rect = parentNodeDom.getBoundingClientRect ? parentNodeDom.getBoundingClientRect() : { width: 0 }
          // 
          const style = window.getComputedStyle(parentNodeDom)
          //  padding  border 
          const paddingLeft = parseFloat(style.paddingLeft)
          const paddingRight = parseFloat(style.paddingRight)
          const borderLeft = parseFloat(style.borderLeftWidth)
          const borderRight = parseFloat(style.borderRightWidth)

          init(e.clientX, rect.width - paddingLeft - paddingRight - borderLeft - borderRight) // 
        },
      }}
    >
      {imageVnode}

      {/*  DOM  */}
      <div className="w-e-image-dragger left-top"></div>
      <div className="w-e-image-dragger right-top"></div>
      <div className="w-e-image-dragger left-bottom"></div>
      <div className="w-e-image-dragger right-bottom"></div>
    </div>
  )
}

function renderImage(elemNode: SlateElement, children: VNode[] | null, editor: IDomEditor): VNode {
  const {
    src, alt = '', href = '', style = {},
  } = elemNode as ImageElement
  const { width = '', height = '' } = style
  const selected = DomEditor.isNodeSelected(editor, elemNode) // 

  const imageStyle: any = { maxWidth: '100%' }

  if (width) { imageStyle.width = '100%' }
  if (height) { imageStyle.height = '100%' }

  // void node renderElem  children core 
  const vnode = <img style={imageStyle} src={src} alt={alt} data-href={href} />

  const isDisabled = editor.isDisabled()

  if (selected && !isDisabled) {
    //  -  resize container
    return renderResizeContainer(editor, elemNode, vnode, { width, height })
  }

  //  image container
  return renderContainer(editor, elemNode, vnode, { width, height })
}

const renderImageConf = {
  type: 'image', //  elemNode.type 
  renderElem: renderImage,
}

export { renderImageConf }
