/**
 * @description parse style html
 * @author wangfupeng
 */

import { DomEditor, IDomEditor } from '@wangeditor-next/core'
import { Descendant, Element } from 'slate'

import { CodeElement } from '../custom-types'
import $, { DOMElement } from '../utils/dom'

export function parseCodeStyleHtml(
  elem: DOMElement,
  node: Descendant,
  _editor: IDomEditor,
): Descendant {
  const $elem = $(elem)

  if (!Element.isElement(node)) { return node }
  if (DomEditor.getNodeType(node) !== 'code') { return node } //  pre/code 

  const elemNode = node as CodeElement

  const langAttr = $elem.attr('class') || ''

  if (langAttr.indexOf('language-') === 0) {
    // V5  class="language-javascript"
    elemNode.language = langAttr.split('-')[1] || '' //  'javascript'
  } else {
    //  V4  class="Javascript"
    elemNode.language = langAttr.toLowerCase()
  }

  return elemNode
}
